.contactsBlock {
    min-height: 60vh;
    background: #2b3940;
    padding: 50px 0;
}

.contactContainer {
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
}

.contactsWrapper {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 40px 0;
    flex-wrap: wrap;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.textarea {
    width: 100%;
    height: 200px;
    border-radius: 10px;
    padding: 15px;
    font-size: 18px;
    font-family: "Open Sans", sans-serif;
}


.inputBlock {
    display: flex;
    gap: 10px;
}

.input {
    width: 100%;
    height: 50px;
    border-radius: 10px;
    padding: 15px;
    font-size: 16px;
}

.errorInput {
    border: 3px solid #00b074;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    padding: 15px;
    font-size: 16px;
    outline: none;
}

.errorMessage {
    border: 3px solid #00b074;
    width: 100%;
    height: 200px;
    border-radius: 10px;
    padding: 15px;
    font-size: 16px;;
    outline: none;
}


.send {
    cursor: pointer;
    align-self: end;
    font-size: 16px;
    background: #00b074;
    width: 150px;
    height: 40px;
    color: white;
    border-radius: 10px;
}

.send:hover {
    color: white;
    background: #00b074;
}

.send:active {
    width: 148px;
    height: 38px;
    opacity: 0.5;
    transition: all 0.3s ease 0s;
}

.title {
    background: white;
}

.inputContainer {
    display: block;
    flex-direction: column;
    justify-content: end;
    width: 100%;
}

.error {
    color: #00b074;
    margin-bottom: 5px;
}

.response {
    color: white;
    font-size: 30px;
    padding: 10px 30px;
    border: 2px solid #00b074;
    position: absolute;
    width: 300px;
    height: 70px;
    margin-top: 10px;
    text-align: center;
}

@media screen and (max-width: 766px) {
    .form {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;
    }

    .inputBlock {
        flex-direction: column;
        gap: 1px;
    }

    .inputContainer {
        display: block;
        flex-direction: column;
        justify-content: end;
        width: 100%;
    }

    .error {
        color: #00b074;
        position: absolute;
        margin-top: 10px;
    }
    .in{
        height: 30px;
    }
}