@import "src/common/style/sass/variables.module";

.project {
  border-radius: 10px;
  background: $primaryColor;
  width: 550px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .imgContainer {
    width: 100%;
    min-height: 250px;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 10px 10px 0 0;
    position: relative;
    z-index: 0;
    transition: 0.9s ease-in-out;

    .img {
      width: 100%;
      height: 350px;
      transition: 0.4s ease-in-out;
      @media screen and (max-width: 760px) {
        height: 100%;
      }
    }
    &:hover .img{
      -webkit-filter: brightness(30%);
      filter: brightness(30%);
      opacity: 1;
      z-index: 1;
    }

    .link {
      background: $primaryColor;
      align-items: center;
      position: absolute;
      opacity: 0;
      text-decoration: none;
      color: white;
      border-radius: 10px;
      transition: 0.3s ease;
      z-index: 1;
    }
    &:hover .link{
      transition: 0.5s ease-in-out;
      padding: 18px 30px;
      border-radius:10px;
      font-size: 16px;
      opacity: 1;
      z-index: 1;
    }
  }

  .textProject {
    min-height: 80px;
    width: 100%;
    color: $textColor1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin: 20px 0;
    @media screen and (max-width: 760px) {
      min-height: 50px;
      padding: 20px;
      margin: 0;
    }
  }

  .textProject h2 {
    margin: 0;
  }

  .description {
    max-width: 350px;
    min-height: 10px;
    word-wrap: break-word;
    justify-content: center;
    display: inline-block;
    text-align: center;
  }

  @media screen and (max-width: 760px) {
    width: 100%;
  }
}






