@import "src/common/style/sass/variables.module";

.container{
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 50px;

.dataWrapper {
  display: flex;
  gap: 20px;
  height: 40px;

  .wrapStyle {
    align-items: center;
    background: white;
    border-radius: 50%;
    width: 40px;
    height: 40px;

    .img {
      align-items: center;
      padding: 10px;
      height: 40px;
      width: 40px;
    }

  }

  .infoData {
    display: flex;
    gap: 5px;
    justify-content: space-between;
    flex-direction: column;
    color: white;
    .title{
      color: $primaryColor;
    }
  }

}
  a {
    text-decoration: none;
    color: white;
  }

}
