*, *:before,*:after{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
}

html,body{
    height: 100%;
    font-family: "Open Sans",sans-serif;
}

@media (max-width: 767px) {
    .appWrapper.active:after {
        position: absolute;
        content: "";
        background-color: black;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0.5;
        transition: all 0.8ms ease 0s;
    }

}