.mainBlock {
  min-height: 100vh;
  background-color: #e2e2e2;
  padding-top: 100px;
  @media (max-width: 767px) {
    background-color: #e2e2e2;
    padding-top: 100px;
    transition: all 0.8ms ease 0s;
  }

  .particles {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }

  .container {
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    .text {
      overflow: hidden;
      width: 450px;
      z-index: 2;

      .heading {
        font-size: 25px;
        color: #4d4d4d;
        opacity: 1;
        margin: 0 0 8px;
        @media screen and (max-width: 500px) {
          text-align: center;
        }
      }
    }

    .name {
      font-size: 50px;
      text-transform: capitalize;
      font-weight: 900;
      margin-bottom: 15px;
      margin-top: 10px;
      @media screen and (max-width: 500px) {
        text-align: center;
      }
    }

    .photoContainer {
      z-index: 3;
      .photo {
        margin-top: 20px;
        width: auto;
        height: 550px;
        padding: 7px;
        border-radius: 30px 0 30px 0;
        box-shadow: #4d4d4d 12px 12px 10px;
        background: #00b074;
        @media screen and (max-width: 500px) {
          margin: 0 0 20px 0;
          width: 100%;
          height: 100%;
        }
      }
    }

  }
}

